,CACA,UAAA,CACA,WAAA,CACA,eAAA,CAGD,iCACC,iBAAA,CACA,QAAA,CACA,MAAA,CACA,YAAA,CACA,UAAA,CACA,+HAAA,CACA,UAAA,CACA,iBAAA,CACA,cAAA,CACA,kBAAA,CACA,eAAA,CACA,sBAAA,CAGD,4CACC,SAAA,CACA,gBAAA,CAGD,sHAEC,YAAA,CACA,OAAA,CAGD,4HAEC,YAAA,CACA,OAAA,CAID,iGACC,gBAAA,CAGD,6KAEC,2BAAA,CACA,kCAAA,CAGD,0BACC,GACC,SAAA,CAED,IACC,UAAA,CAED,KACC,SAAA,CAAA","sourcesContent":["\n@import '../../assets/variables';\n\n.call-view {\n\twidth: 100%;\n\theight: 100%;\n\toverflow: hidden;\n\tbackground-color: $color-call-background;\n}\n\n#call-container {\n\twidth: 100%;\n\theight: 100%;\n\tbackground-color: $color-call-background;\n\n\t&.blurred {\n\t\tbackdrop-filter: blur(25px);\n\t}\n}\n\n#videos {\n\tposition: absolute;\n\twidth: 100%;\n\theight: calc(100% - 60px);\n\ttop: 60px;\n\toverflow: hidden;\n\tdisplay: -webkit-box;\n\tdisplay: -moz-box;\n\tdisplay: -ms-flexbox;\n\tdisplay: -webkit-flex;\n\tdisplay: flex;\n\t-webkit-justify-content: space-around;\n\tjustify-content: space-around;\n\t-webkit-align-items: flex-end;\n\talign-items: flex-end;\n\tflex-direction: column;\n\tpadding: 0 8px 8px 8px;\n}\n\n.video__promoted {\n\tposition:relative;\n\theight: 100%;\n\twidth: 100%;\n\tdisplay: block;\n}\n\n.video__promoted.full-page {\n\t/* make the promoted video cover the whole call view */\n\tposition: static;\n}\n\n.local-video {\n\tposition: absolute;\n\tright: 0;\n\tbottom: 0;\n\twidth: 300px;\n\theight: 250px;\n\t&--sidebar {\n\t\twidth: 150px;\n\t\theight: 100px;\n\t}\n}\n\n#videos.hidden {\n\tdisplay: none;\n}\n\n:deep(video) {\n\tz-index: 0;\n\t/* default filter for slightly better look */\n\t/* Disabled for now as it causes a huuuuge performance drop.\n\t CPU usage is more than halved without this.\n\t -webkit-filter: contrast(1.1) saturate(1.1) sepia(.1);\n\t filter: contrast(1.1) saturate(1.1) sepia(.1);\n\t */\n\tvertical-align: top; /* fix white line below video */\n}\n\n#videos .videoContainer :deep(.avatardiv) {\n\tbox-shadow: 0 0 15px var(--color-box-shadow);\n}\n\n.participants-1 #videos .videoContainer :deep(video),\n.participants-2 #videos .videoContainer :deep(video) {\n\tpadding: 0;\n}\n\n.videoContainer :deep(.avatar-container .avatardiv) {\n\tdisplay: block;\n\tmargin-left: auto;\n\tmargin-right: auto;\n}\n\n.videoContainer.promoted :deep(.avatar-container) {\n\ttop: 30%;\n}\n\n.videoContainer.promoted :deep(.avatar-container + .nameIndicator) {\n\tdisplay: none;\n}\n\n.videoContainer.promoted :deep(.mediaIndicator) {\n\tdisplay: none !important;\n}\n\n@media only screen and (max-width: 768px) {\n\t.participants-1 .videoView,\n\t.participants-2 .videoView {\n\t\tmax-height: 35%;\n\t}\n}\n\n.participants-1 .videoView :deep(video),\n.participants-2 .videoView :deep(video) {\n\tposition: absolute;\n\tmax-height: 100% !important;\n\tbottom: 0;\n\tborder-top-right-radius: 3px;\n\tright: 0;\n}\n\n#screens {\n\tposition: relative;\n\twidth: 100%;\n\theight: 100%;\n\toverflow: hidden;\n}\n\n:deep(.nameIndicator) {\n\tposition: absolute;\n\tbottom: 0;\n\tleft: 0;\n\tpadding: 12px;\n\tcolor: #fff;\n\ttext-shadow: 3px 3px 10px rgba(0, 0, 0, .5), 3px -3px 10px rgba(0, 0, 0, .5), -3px 3px 10px rgba(0, 0, 0, .5), -3px -3px 10px rgba(0, 0, 0, .5);\n\twidth: 100%;\n\ttext-align: center;\n\tfont-size: 20px;\n\twhite-space: nowrap;\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n}\n\n:deep(.videoView .nameIndicator) {\n\tpadding: 0;\n\toverflow: visible;\n}\n\n.participants-1 .videoView :deep(.nameIndicator),\n.participants-2 .videoView :deep(.nameIndicator) {\n\tleft: initial;\n\tright: 0;\n}\n\n.participants-1 .videoView :deep(.avatar-container),\n.participants-2 .videoView :deep(.avatar-container) {\n\tleft: initial;\n\tright: 0;\n}\n\n/* ellipsize name in 1on1 calls */\n.participants-2 :deep(.videoContainer.promoted + .videoContainer-dummy .nameIndicator) {\n\tpadding: 12px 35%;\n}\n\n#videos .videoContainer.speaking:not(.videoView) :deep(.nameIndicator),\n#videos .videoContainer.videoView.speaking :deep(.nameIndicator .microphone-icon) {\n\tanimation: pulse 1s;\n\tanimation-iteration-count: infinite;\n}\n\n@keyframes pulse {\n\t0% {\n\t\topacity: 1;\n\t}\n\t50% {\n\t\topacity: .3;\n\t}\n\t100% {\n\t\topacity: 1;\n\t}\n}\n\n","/**\n * @copyright Copyright (c) 2019 John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @author John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n@use 'sass:math';\n\n// https://uxplanet.org/7-rules-for-mobile-ui-button-design-e9cf2ea54556\n// recommended is 48px\n// 44px is what we choose and have very good visual-to-usability ratio\n$clickable-area: 44px;\n\n// background icon size\n// also used for the scss icon font\n$icon-size: 16px;\n\n// icon padding for a $clickable-area width and a $icon-size icon\n// ( 44px - 16px ) / 2\n$icon-margin: math.div($clickable-area - $icon-size, 2);\n\n// transparency background for icons\n$icon-focus-bg: rgba(127, 127, 127, .25);\n\n// popovermenu arrow width from the triangle center\n$arrow-width: 9px;\n\n// opacities\n$opacity_disabled: .5;\n$opacity_normal: .7;\n$opacity_full: 1;\n\n// menu round background hover feedback\n// good looking on dark AND white bg\n$action-background-hover: rgba(127, 127, 127, .25);\n\n// various structure data used in the\n// `NcAppNavigation` component\n$header-height: 50px;\n$navigation-width: 300px;\n\n// mobile breakpoint\n$breakpoint-mobile: 1024px;\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message max width\n$message-max-width: 680px;\n\n//message utils width\n$message-utils-width: 100px;\n\n//message form max height\n$message-form-max-height: 180px;\n\n$color-call-background: rgba(34, 34, 34, 0.8);\n$color-guests-avatar: #ffffff;\n$color-background-guests-avatar: #b9b9b9;\n\n\n//chat font size\n$chat-font-size: 15px;\n\n//chat line height\n$chat-line-height: 1.6em;\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
