,CAGD,oEAEC,uBAAA,CACA,sBAAA,CACA,iBAAA,CACA,uDAAA,CACA,eAAA,CACA,YAAA,CACA,qBAAA,CACA,eAAA,CACA,sCAAA,CAGD,sCACC,iBAAA,CACA,uBAAA,CACA,uBAAA,CACA,YAAA,CACA,qBAAA,CAEA,oDACC,WAAA,CAIF,wCACC,wCAAA,CAGD,uDAEC,WAAA,CACA,UAAA,CAGD,kDACC,WAAA,CACA,UAAA,CACA,sBAAA,CAGD,6BAEC,kBAAA,CAGD,8BAEC,gBAAA,CAGD,mCACC,WAAA,CAGD,+BACC,iBAAA,CACA,WAAA,CACA,UAAA,CACA,KAAA,CACA,MAAA,CACA,+BAAA,CACA,cAAA,CACA,mDAAA,CAGD,6BACC,iBAAA,CACA,QAAA,CACA,UAAA,CACA,wBAAA,CACA,YAAA,CACA,sBAAA,CACA,kBAAA,CACA,WAAA,CACA,kCACC,sBAAA,CACA,WAAA,CAED,iDACC,UAAA,CACA,6CAAA,CACA,8GAEC,SAAA","sourcesContent":["/**\n * @copyright Copyright (c) 2020 Joas Schilling <coding@schilljs.com>\n *\n * @author Joas Schilling <coding@schilljs.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n@use 'sass:math';\n\n@import 'variables';\n\n\n@mixin avatar-mixin($size: 32px) {\n\t.avatar-#{$size} {\n\t\tposition: sticky;\n\t\ttop: 0;\n\t\twidth: $size;\n\t\theight: $size;\n\t\tline-height: $size;\n\t\tfont-size: math.div($size, 2);\n\t\tborder-radius: 50%;\n\n\t\t&.icon {\n\t\t\tborder-radius: 50%;\n\t\t\theight: $size;\n\t\t\twidth: $size;\n\t\t\tbackground-color: var(--color-background-darker);\n\t\t}\n\n\t\t&.bot {\n\t\t\tpadding-left: 5px;\n\t\t\tline-height: $size;\n\t\t\tborder-radius: 50%;\n\t\t\tbackground-color: var(--color-background-darker);\n\t\t}\n\n\t\t&.guest {\n\t\t\tline-height: $size;\n\t\t\tfont-size: math.div($size, 2);\n\t\t\tcolor: $color-guests-avatar;\n\t\t\tbackground-color: $color-background-guests-avatar;\n\t\t\tpadding: 0;\n\t\t\tdisplay: block;\n\t\t\ttext-align: center;\n\t\t\tmargin-left: auto;\n\t\t\tmargin-right: auto;\n\t\t}\n\t}\n}\n","\n@import '../../../assets/variables';\n@import '../../../assets/avatar';\n@include avatar-mixin(64px);\n@include avatar-mixin(128px);\n\n.not-connected {\n\tvideo,\n\t.avatar-container {\n\t\topacity: 0.5;\n\t}\n}\n\n// Always display the local video in the last row\n.localVideoContainer {\n\tgrid-row-end: -1;\n\tborder-radius: calc(var(--default-clickable-area)/2);\n\tz-index: 1;\n}\n\n.video-container-grid {\n\tposition:relative;\n\theight: 100%;\n\twidth: 100%;\n\toverflow: hidden;\n\tdisplay: flex;\n\tflex-direction: column;\n}\n\n.video-container-stripe:not(.local-video--sidebar) {\n\t// aspect-ratio is set according to the maximum video resolution after applying constraints (720*540)\n\t--aspect-ratio: 1.33333;\n\t--stripe-height: 242px;\n\tposition: relative;\n\tflex: 0 0 calc(var(--aspect-ratio) * var(--stripe-height));\n\toverflow: hidden;\n\tdisplay: flex;\n\tflex-direction: column;\n\tmargin-top: auto;\n\theight: var(--stripe-height) !important;\n}\n\n.video-container-big {\n\tposition: absolute;\n\twidth: calc(100% - 16px);\n\theight: calc(100% - 8px);\n\tdisplay: flex;\n\tflex-direction: column;\n\n\t& .videoWrapper {\n\t\tmargin: auto;\n\t}\n}\n\n.video-container-small {\n\tborder-radius: var(--border-radius-large);\n}\n\n.videoWrapper,\n.video {\n\theight: 100%;\n\twidth: 100%;\n}\n\n.videoWrapper.icon-loading:after {\n\theight: 60px;\n\twidth: 60px;\n\tmargin: -32px 0 0 -32px;\n}\n\n.video--fit {\n\t/* Fit the frame */\n\tobject-fit: contain;\n}\n\n.video--fill {\n\t/* Fill the frame */\n\tobject-fit: cover;\n}\n\n.avatar-container {\n\tmargin: auto;\n}\n\n.hover-shadow {\n\tposition: absolute;\n\theight: 100%;\n\twidth: 100%;\n\ttop: 0;\n\tleft: 0;\n\tbox-shadow: inset 0 0 0 3px white;\n\tcursor: pointer;\n\tborder-radius: calc(var(--default-clickable-area)/2);\n}\n\n.bottom-bar {\n\tposition: absolute;\n\tbottom: 0;\n\twidth: 100%;\n\tpadding: 0 20px 12px 24px;\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\theight: 40px;\n\t&--big {\n\t\tjustify-content: center;\n\t\theight: 48px;\n\t}\n\t& &__button {\n\t\topacity: 0.8;\n\t\tbackground-color: var(--color-background-dark);\n\t\t&:hover,\n\t\t&:focus {\n\t\t\topacity: 1;\n\t\t}\n\t}\n}\n","/**\n * @copyright Copyright (c) 2019 John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @author John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n@use 'sass:math';\n\n// https://uxplanet.org/7-rules-for-mobile-ui-button-design-e9cf2ea54556\n// recommended is 48px\n// 44px is what we choose and have very good visual-to-usability ratio\n$clickable-area: 44px;\n\n// background icon size\n// also used for the scss icon font\n$icon-size: 16px;\n\n// icon padding for a $clickable-area width and a $icon-size icon\n// ( 44px - 16px ) / 2\n$icon-margin: math.div($clickable-area - $icon-size, 2);\n\n// transparency background for icons\n$icon-focus-bg: rgba(127, 127, 127, .25);\n\n// popovermenu arrow width from the triangle center\n$arrow-width: 9px;\n\n// opacities\n$opacity_disabled: .5;\n$opacity_normal: .7;\n$opacity_full: 1;\n\n// menu round background hover feedback\n// good looking on dark AND white bg\n$action-background-hover: rgba(127, 127, 127, .25);\n\n// various structure data used in the\n// `NcAppNavigation` component\n$header-height: 50px;\n$navigation-width: 300px;\n\n// mobile breakpoint\n$breakpoint-mobile: 1024px;\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message max width\n$message-max-width: 680px;\n\n//message utils width\n$message-utils-width: 100px;\n\n//message form max height\n$message-form-max-height: 180px;\n\n$color-call-background: rgba(34, 34, 34, 0.8);\n$color-guests-avatar: #ffffff;\n$color-background-guests-avatar: #b9b9b9;\n\n\n//chat font size\n$chat-font-size: 15px;\n\n//chat line height\n$chat-line-height: 1.6em;\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
